        "Este posibil ca noi să luăm infirmităţile altora aşa cum a făcut Domnul nostru? Şi a fost vitalitatea lui Cristos folosită pentru Biserică? Pag. 632 p. 1, 2",
        "Care este natura şi costurile „suferinţelor lui Cristos” care sunt îndurate de membrii Corpului Său? Pag. 633 p. 1, 2",
        "Cum putem face deosebire între suferinţele pentru dreptate şi suferinţele pentru indiscreţie sau fapte rele? Pag. 633 p. 3",
        "Dacă bolile trupului vin dintr-o cauză nepăcătoasă sau neegoistă, cum trebuie ele pri­mite şi îndurate de către Noua Creatură? Pag. 634 p. 1",
        "Cum trebuie să-şi privească Noua Creatură corpul ei muritor, şi ce libertate are cu privire la folosirea remediilor legitime pentru calmarea sau vindecarea bolii? Pag. 634 p. 2; pag. 635 p. 1",
        "Având în vedere minunile Domnului nostru, ar trebui să luăm ca exemplu pe cei vindecaţi sau pe Vindecător? Pag. 636 p. 1",
        "Ar fi fost corect ca Domnul nostru să-Şi fi folosit puterile spirituale pentru nevoile Sale naturale? Şi n-ar trebui ca Biserica să-I urmeze paşii în această privinţă? Pag. 636 p. 2; pag. 637 p. 1",


        "Pe ce Scripturi se sprijină în principal cei care vindecă prin credinţă? Şi cum sunt ele aplicate greşit? Pag. 637 p. 2; pag. 638 p. 1",
        "Cărei puteri au atribuit fariseii minunile Domnului nostru şi care a fost răspunsul Lui? Pag. 638 p. 2",
        "Care a fost scopul principal al miracolelor de la începutul acestui Veac, şi de ce trebuie să privim cu suspiciune aşa-numitele miracole de la sfârşitul Veacului? Pag. 638 p. 3; pag. 639 p. 1",
        "Care este proba sigură a tuturor acestor înşelări? Pag. 639 p. 2",
        "Dacă cei care hulesc numele lui Dumnezeu fac lucrări minunate de vindecare, să presupunem că Domnul sprijină acum învăţăturile lor false? Pag. 640 p. 1",
        "Presupunând că alţii care vindecă boli sunt zeloşi în lucrarea misionară, ar trebui ca acest lucru să ne schimbe opinia despre respectiva mişcare în general? Pag. 640 p. 2",

        // 2022.05.17
        "Care sunt dovezile că Satan aproape a ajuns la limită? Pag. 641 p. 1",
        "Cum să înţelegem filosofia procedurilor lui Dumnezeu cu Noua Creaţie prin supunerea lor la astfel de încercări de foc, deosebite? Pag. 642; pag. 643 p.1",
        "Ce ilustraţie din natură ne poate ajuta să înţelegem procedurile providenţiale ale lui Dumnezeu faţă de Noua Creaţie, şi ce speranţă avem noi în răsturnarea definitivă a răului când acesta a servit scopului Divin? Pag. 643 p.2",
